}

:root {
	/* Primary Colors */
	--primary-color: #28328c;
	--primary-gradient: linear-gradient(to right, #333fa4, #151c5a);

	/* Secondary Colors */
	--secondary-purple-1: #8a45e6;
	--secondary-pink-1: #ffdef7;
	--secondary-blue-1: #1ebef0;
	--secondary-blue-2: #afcfed;
	--secondary-green-2: #98cbd6;
	--secondary-gray-1: #ccd0db;
	--secondary-blue-2: #a2cae7;
	--secondary-purple-2: #d5d8fc;
	--secondary-purple-3: #edf1ff;
	--secondary-white-2: #f8f8fb;
	--secondary-face-2: #f8e9e6;

	/* Text Colors */
	--font-black: #2d2d32;
	--font-gray: #b4b4c4;

	/* Font Size */
	font-size: 62.5%;
}

body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #f1f0f5;
}

* {
	/* font-family: 'PT Sans', sans-serif; */
	font-family: 'Camphor', 'PT Sans', sans-serif;
}

*,
:after,
:before,
html {
	box-sizing: border-box;
}

code {
	/* font-family: 'PT Sans', sans-serif; */
	font-size: 1.4rem;
}

h1,
h2,
h3,
h4,
p,
li {
	list-style: none;
	margin: 0;
	padding: 0;
}

img {
	width: 100%;
}

/* Helper Classes */
.txt-gray {
	color: #b4b4c4;
	color: var(--font-gray);
}

.txt-black {
	color: #2d2d32;
	color: var(--font-black);
}

.txt-blue {
	color: #1ebef0;
	color: var(--secondary-blue-1);
}

.mt-60 {
	margin-top: 60px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number]{
	-moz-appearance: textfield;
}
.App {
	/* background-color: #cfcfcf; */
}

.bg-white {
	background-color: #fff;
}

.content {
	width: 1180px !important;
	zoom: 1 !important;
	margin: auto !important;
}

.DownloadWrapper {
	height: 572px;
	background-color: #f8f8fb;
	position: relative;
}

.rupees_symbol {
	font-family: 'Roboto', sans-serif;
}

.mobile_list {
	position: relative;
	width: 100%;
	background-color: var(--primary-color);
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	left: 100%;
	transition: left .5s linear;
}

.mobile_list>.close_icon {
	position: absolute;
	right: 3rem;
	top: 3rem;
	font-size: 3rem;
	color: white;

}

.mobile_list::before,
.mobile_list::after {
	display: none;
}

.mobile_list>ul {
	margin: 0;
	padding: 0;
}

.mobile_list>ul>li {
	text-align: center;
	padding: 1rem 2rem;
}

.mobile_list>ul>li:hover {
	background-color: white;
}

.mobile_list>ul>li:hover>a {
	color: var(--primary-color);
}

.mobile_list>ul>li>a {
	color: white;
	font-size: 3rem;
	text-decoration: none;
	font-weight: 400;
	font-family: 'CamphorBold';
}

#hamburger_icon {
	display: none;
	-webkit-appearance: none;
	        appearance: none;
}

#hamburger_icon:checked~.mobile_list {
	left: 0;
}

#hamburger_icon:checked~.hamburger_menu {
	display: none;
}

.hamburger_menu {
	display: none;
	font-size: 3rem;
	width: 4rem;
	height: 4rem;
	position: absolute;
	right: 0;
	top: 2rem;
	color: var(--primary-color);
}



@media (max-width: 1200px) {

	.content {
		width: 100% !important;
	}

	#medicines,
	#lab_tests,
	#surgeries {
		display: none !important;
	}

	#try_plus_ad {
		display: none !important;
	}

	#nav_mid {
		display: none;
	}

	#cards_content {
		width: 100% !important;
	}

	#searchbar_content {
		width: 100% !important;
	}

	#searchbar_wrapper {
		width: 90% !important;
		margin: 0 auto;
	}

}

@media (max-width: 600px) {

	.content {
		width: 100% !important;
		text-align: center;
	}

	.hamburger_menu {
		display: block;
	}

	.nav>.wrapper {
		display: flex !important;
		justify-content: center !important;
		align-items: center !important;
	}

	.nav>.wrapper>.nav-left {
		text-align: center;
	}

	.nav>.wrapper>div:last-child {
		display: none !important;
	}

	.Card {
		margin-bottom: 1rem;
	}

	.head {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
	}

	.head>.btn {
		position: unset !important;
	}

	#cards_content {
		flex-direction: column;
		align-items: center;
	}

	#card_content>div {
		margin-bottom: 1rem !important;
	}

	#consult_doc {
		flex-direction: column;
		display: flex;
		align-items: center;
	}

	#download_left {
		display: none;
	}

	#download_right {
		width: 100%;
		margin: 0 auto;
		padding: 1rem 2rem;
		/* text-align: center; */
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
	}

	#searchbar_content {
		width: 100%;
	}

	#searchbar_wrapper {
		width: 90%;
		margin: 0 auto;
	}

	#banner_wrapper {
		min-height: unset;
	}

	#footer_section {
		min-width: 100vw !important;
		width: 100%;
	}

	#footer_top {
		width: 100% !important;

	}

	.footerdata {
		min-width: 163px;
		padding: 1rem;
	}

	.footerdata>h3 {
		margin-top: 3rem;
	}


	#download {
		display: block;
	}

	#downloadsub2 {
		margin-left: 13% !important;
	}

	#beforedownload {
		/* margin-left: 12% !important; */
	}

	#benifit_card {
		margin-left: 5% !important;
	}

	#content {
		width: 95% !important;
	}

	#pinkbanner {
		height: 45% !important;
		width: 62% !important;
	}

	#pinkheading {
		font-size: 20px;
	}

	#pinksub {
		font-size: 12px;
	}

	#active {
		font-size: 10px;
	}

	#add {
		font-size: 12px;
		display: block !important;
	}

	.ctrl-btn {
		display: none !important;
	}

}
